import { Injectable, OnInit, OnDestroy } from '@angular/core';
import {UserSettingsService} from './user-settings.service';
import {InteractionService} from './interaction.service';
import * as io from 'socket.io-client';


@Injectable()
export class LogService implements OnInit, OnDestroy {
  constructor(private user: UserSettingsService,
              private interaction: InteractionService) {
    this.connect();
  }
  private _socket = null;
  items = [];
  connect() {
    const self = this;
    if (!self._socket) {
      self.interaction.getLog((err, pre) => {
        self.items.push.apply(self.items, pre || []);
        const url = self.user.settings['host'];
        self._socket = io(url);
        self._socket.on('log', (log) => {
          self.items.push(log);
        });
        self._socket.on('error', console.error);
      });
    }
  }
  disconnect() {
    if (!!this._socket) {
      this._socket.disconnect();
      this._socket = null;
    }
  }
  isActive = () => !!this._socket;
  ngOnInit() {

  }
  ngOnDestroy() {
    this.disconnect();
    this.items.splice(0);
  }
}



