import { Injectable, OnDestroy } from '@angular/core';
import { UtilsService } from './utils.service';
import _ from 'lodash';

const STORE_KEY = 'ECHO-SERVICE2-USER-SETTINGS';
const DEFAULT_SERVER_HOST = 'http://localhost:9001';

@Injectable()
export class UserSettingsService implements OnDestroy {
  constructor(private u: UtilsService) {
    const sts = this.u.storage.get(STORE_KEY) || {};
    _.extend(this.settings, sts);
    _.defaults(this.settings, {
      host: DEFAULT_SERVER_HOST,
      debug: false
    });
  }
  settings = {};
  update() {
    this.u.storage.set(STORE_KEY, this.settings);
  }
  getUrl(...parts: any[]) {
    const args = Array.prototype.slice.call(arguments);
    args.unshift(this.settings['host'] || DEFAULT_SERVER_HOST);
    return this.u.checkUrl.apply(null, args);
  }
  ngOnDestroy() {
    this.update();
  }
}



