import { Injectable } from '@angular/core';
import { UtilsService } from './utils.service';
import { AppPagesService } from './app-pages.service';
import {InteractionService} from './interaction.service';
import _ from 'lodash';



@Injectable()
export class CartService {
  constructor(private u: UtilsService,
              private app: AppPagesService,
              private interaction: InteractionService) { }
  items = [];
  scenario = null;
  add(o) {
    if (!o) { return; }
    let type = _.find(this.items, i => i.type === o._type);
    if (!type) {
      const page = _.find(this.app.pages, p => p.type === o._type);
      type = {
        title: (page || {}).caption || o._type || 'unknown',
        type: o._type,
        icon: (page || {}).icon || 'label_outline',
        items: []};
      this.items.push(type);
    }
    const xo = _.find(type.items, i => i._id === o._id);
    if (!xo) { type.items.push(o); }
  }
  count() {
    let c = 0;
    this.items.forEach(i => c += i.items.length);
    return c;
  }
  clear(o?: any) {
    if (o) {
      // if (this.items[o._type] && this.items[o._type][o._id]) {
      //   delete this.items[o._type][o._id];
      // }
    } else {
      this.items.splice(0);
    }
  }
  selection() {
    const selection = [];
    this.items.forEach(i => {
      i.items.forEach(ii => {
        if (!!ii.selected) { selection.push(ii); }
      });
    });
    return selection;
  }
  toggleSelection() {
    const sel = this.selection();
    if (sel.length) {
      sel.forEach(i => i.selected = false);
    } else {
      this.items.forEach(i => i.items.forEach(ii => ii.selected = true));
    }
  }
  removeSelection() {
    this.items.forEach(i => _.remove(i.items, ii => ii.selected));
  }
  contains(o) {
    return _.find(this.items, i => _.find(i.items, ii => ii._id === o._id));
  }
  push() {
    const sel = this.selection();
    this.interaction.push(sel, this.scenario);
  }
}



