import { Injectable } from '@angular/core';
import { InteractionService } from './interaction.service';
import { AppPagesService } from './app-pages.service';
import { Router, NavigationEnd } from '@angular/router';
import { UtilsService } from './utils.service';
import _ from 'lodash';

export class DefaultOptions {
  namePrp = 'name';
  icon = 'label';
  template = null;
  type = '';
  showAll = false;
  description = null;
  constructor() {}
  onSelect: () => {};
}

const route_cache = {
  id: null
};

@Injectable()
export class BrowserService {
  constructor(private interaction: InteractionService,
              private app: AppPagesService,
              private router: Router,
              private u: UtilsService) {
    const self = this;
    self.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        self.u.parseUrlId(event['url'], (url, id) => {
          if (route_cache.id && route_cache.id !== id) {
            self.navigateById(id);
          }
        });
      }
    });
  }
  options: any = new DefaultOptions();
  onChanged = null;
  documents = [];
  _documents: any = [];
  currentInfo = null;
  current = null;
  current_str = null;
  idle = true;
  loading = false;
  _startId = null;
  _refresh() {
    const self = this;
    self.documents = ((self.options || {}).type && !self.options.showAll) ?
      _(self._documents).filter(d => d._type === self.options.type).value() :
      self._documents;
    const cur = (self.current || {})._id || self._startId;
    const tosel = cur ? _.find(self.documents || [], d => d._id === cur) : null;
    self.select(tosel);
    if (_.isFunction(self.onChanged)) {
      self.onChanged(self.documents);
    }
    self._startId = null;
  }
  newElement() {
    const self = this;
    const doc: any = {};
    const baseDoc = self.u.clone(self.options.template);
    _.extend(doc, baseDoc);
    doc._id = self.u.guid();
    doc._type = self.options.type;
    doc._new = true;
    if (_.has(doc, 'name')) {
      doc.name = this.u.getNewName(self._documents, doc.name);
    }
    self._documents.push(doc);
    self.current = doc;
    this._refresh();
    this.onSelectionChanged();
  }
  refresh(deep?: boolean) {
    const self = this;
    self.idle = true;
    if (deep || this._documents.length <= 0) {
      self.interaction.getDocuments().subscribe(docs => {
        self._documents = _.sortBy(docs, self.options.namePrp || 'name');
        self._refresh();
        self.idle = false;
        self.onLoaded();
      }, err => {
        self.u.error(err);
        self.idle = false;
        self.onLoaded();
      });
    } else {
      self._refresh();
      self.idle = false;
    }
  }
  register(o) {
    const self = this;
    self.u.parseUrlId(self.router.url, (url, id) =>  self._startId = id);
    self.options = new DefaultOptions();
    if (_.isString(o)) {
      o = {type: o};
    }
    _.extend(self.options, o || {});
    const myp = _.find(self.app.pages, p => p.type === o.type);
    if (myp) {
      self.options.icon = myp.icon;
    }
    self.refresh(true);
  }
  onSelectionChanged(o?: any) {
    if (_.isFunction(this.options['onSelect'])) {
      this.options['onSelect'](o);
    }
  }
  onLoaded() {
    if (_.isFunction(this.options['onLoaded'])) {
      this.options['onLoaded'](this._documents);
    }
  }
  deregister() {
    this.documents.splice(0);
    this.current = null;
    this.currentInfo = null;
    this.current_str = null;
    this.options = new DefaultOptions();
  }
  navigateById(id: string) {
    const self = this;
    self.select(_.find(self.documents || [], d => d._id === id));
  }
  navigate(o) {
    const self = this;
    route_cache.id = o._id;
    self.u.parseUrlId(this.router.url, (url, id) => {
      self.router.navigate([url, o._id]);
    });
    this.select(o);
  }
  select(o) {
    const self = this;
    if (self.loading) { return; }
    self.currentInfo = o;
    route_cache.id = (o || {})._id;
    if (!o || o._new) {
      self.current = (o || {})._new ? o : null;
      this.current_str = (o || {})._new ? JSON.stringify(o) : null;
    } else {
      self.loading = true;
      self.interaction.getDocument(o).subscribe(doc => {
        self.current = doc;
        self.current_str = doc ? JSON.stringify(doc, null, 2) : null;
        self.loading = false;
        self.onSelectionChanged(doc);
      }, err => {
        console.error(err);
        self.loading = false;
        self.onSelectionChanged(null);
      });
    }
  }
  save(message?: string) {
    this.interaction.save(this.current, message || 'Element successfully updated!');
  }
  saveAll() {
    const self = this;
    const _new = _.find(self.documents, d => d._new);
    return new Promise((resolve, reject) => {
      if (_new) {
        self.interaction.save(_new, '"' + (_new.name || _new.title || _new._id) + '" successfully updated!', (err) => {
          delete _new._new;
          self.saveAll().then(() => resolve());
        });
      } else {
        resolve();
      }
    });
  }
  private remove(doc) {
    const self = this;
    const ex = _.find(self._documents, d => d._id === doc._id);
    if (ex) {
      _.pull(self._documents, ex);
    }
  }
  private _delete() {
    const self = this;
    self.remove(self.current);
    self.current = null;
    self.current_str = null;
    self._refresh();
    self.onSelectionChanged();
  }
  delete(message?: string) {
    const self = this;
    self.u.confirm({
      message: message || 'Delete current document?',
      ok: 'Ok',
      cancel: 'Cancel'
    }, function(resp){
      if (resp === 'ok') {
        if (self.current._new) {
          self._delete();
        } else {
          self.interaction.delete(self.current).subscribe(() => {
            self._delete();
          }, err => {
            console.error(err);
            self.loading = false;
          });
        }
      }
    });
  }
}



