import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { UserSettingsService } from './user-settings.service';
import { UtilsService } from './utils.service';
import _ from 'lodash';

export class InteractionEvent {
  type: string;
  constructor(type: string) {
    this.type = type;
  }
}

@Injectable()
export class InteractionService {
  constructor(private http: HttpClient,
              private user: UserSettingsService,
              private u: UtilsService) {}
  types = {
    custom: 'custom',
    connections: 'connections',
    queries: 'queries',
    documents: 'documents',
    previews: 'previews'
  };
  events: EventEmitter<InteractionEvent> = new EventEmitter();
  private static _err(cb?: Function) {
    return function(err) {
      if (err) { console.error(err); }
      if (cb) { cb(err); }
    };
  }
  login() {
    // TODO
  }
  logout() {
    // TODO
  }
  getInfo() {
    return this.http.get(this.user.getUrl('api/scenario/current'));
  }
  getLog(cb) {
    this.http.get(this.user.getUrl('api/log'))
      .subscribe(r => cb(null, r), InteractionService._err(cb));
  }
  getScenarios() {
    return this.http.get(this.user.getUrl('api/scenario'));
  }
  getDocuments() {
    return this.http.get(this.user.getUrl('api/scenario/documents'));
  }
  getDocument(o) {
    const id = _.isString(o) ? o : (o.id || o._id);
    return this.http.get(this.user.getUrl('api/scenario/document', id));
  }
  save(o: any, message?: string, cb?: Function) {
    const self = this;
    cb = cb || this.u.noop;
    this.http.post(self.user.getUrl('api/scenario/save'), o)
      .subscribe(r => {
        delete o._new;
        if (message) { self.u.snack(message); }
        cb(null, r);
      }, InteractionService._err(cb));
  }
  setScenario(scenario, cb) {
    this.http.post(this.user.getUrl('api/scenario/apply'), scenario)
      .subscribe(r => cb(null, r), InteractionService._err());
  }
  saveScenario(scenario, cb) {
    this.http.post(this.user.getUrl('api/scenario/update'), scenario)
      .subscribe(r => cb(null, r), InteractionService._err());
  }
  testConnection(o, cb) {
    this.http.post(this.user.getUrl('api/data/test/conn'), o)
      .subscribe(r => cb(), InteractionService._err(cb));
  }
  getProviders() {
    return this.http.get(this.user.getUrl('api/data/providers'));
  }
  getSchema(cnnId, cb) {
    this.http.get(this.user.getUrl('api/data/schema', cnnId))
      .subscribe(r => cb(null, r), InteractionService._err(cb));
  }
  getResult(query, cb) {
    this.http.post(this.user.getUrl('api/data/test/exec'), query)
      .subscribe(result => cb(null, result), InteractionService._err(cb));
  }
  delete(o) {
    return this.http.delete(this.user.getUrl('api/scenario', o._id, o._type));
  }
  deleteNotUsedPreviews() {
    console.log('TODO');
  }
  push(docs, scenario) {
    const self = this;
    self.http.post(self.user.getUrl('api/scenario/push'), {source: docs, target: scenario})
      .subscribe(r => {
        self.u.snack('' + r);
        self.events.emit(new InteractionEvent('push'));
      }, err => self.u.error(err));
  }
  download(folder) {
    const self = this;
    self.http.get(self.user.getUrl('api/scenario/download', folder), { responseType: 'blob' })
      .subscribe(blob => {
        const url = window.URL.createObjectURL(blob);
        if (navigator.msSaveOrOpenBlob) {
          navigator.msSaveBlob(blob, folder + '.zip');
        } else {
          const a = document.createElement('a');
          a.href = url;
          a.download = folder + '.zip';
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
        }
        window.URL.revokeObjectURL(url);
      }, InteractionService._err());
  }
  upload(data, cb) {
    const self = this;
    self.http.post(self.user.getUrl('api/scenario/upload'), data)
      .subscribe(r => {
        self.u.snack('Scenario uploaded!');
        cb();
      }, InteractionService._err(cb));
  }
  getSystemParameters() {
    return this.http.get(this.user.getUrl('api/data/system'));
  }
  api(cb) {
    const self = this;
    self.http.get(self.user.getUrl('api')).subscribe(r => cb(r), err => self.u.error(err));
  }
  templatize(options, cb) {
    const self = this;
    self.http.post(self.user.getUrl('api/data/template'), options)
      .subscribe(r => cb(null, r), InteractionService._err(cb));
  }
  test(o, cb) {
    try {
      const data = o.body ? JSON.parse(o.body) : null;
      const body = (data && ['post', 'put'].indexOf(o.verb) > -1) ? data : null;
      const params = (data && ['get', 'delete'].indexOf(o.verb) > -1) ? this.u.getUrlParams(data) : null;
      this.http[o.verb](this.u.checkUrl(o.host, o.path + (params || '')), body || {})
        .subscribe(r => cb(null, r), err => cb(err));
    } catch (err) {
      cb(err);
    }
  }
}



