import { Injectable } from '@angular/core';
import _ from 'lodash';

@Injectable()
export class AppPagesService {
  constructor() {}
  pages = [
    {
      caption: 'Scenarios',
      type: 'scenario',
      icon: 'work',
      'default': true,
      left: true,
      help: true,
      helpDefault: true
    },
    {
      caption: 'Documents',
      type: 'documents',
      path: '/*',
      icon: 'label',
      left: true,
      help: true
    },
    {
      caption: 'Connections',
      type: 'connections',
      path: '/*',
      icon: 'settings_input_component',
      left: true,
      help: true
    },
    {
      caption: 'Queries',
      type: 'queries',
      path: '/*',
      icon: 'file_download',
      left: true,
      help: true
    },
    {
      tooltip: 'data-entry',
      type: 'dataentry',
      path: '/*',
      icon: 'label',
      hidden: true
    },
    {
      tooltip: 'walking-data',
      type: 'walkingdata',
      path: '/*',
      icon: 'swap_vertical_circle',
      hidden: true
    },
    {
      tooltip: 'previews',
      type: 'previews',
      path: '/*',
      icon: 'image',
      help: true
    },
    {
      tooltip: 'rules',
      type: 'rules',
      path: '/*',
      icon: 'lock',
      hidden: true
    },
    {
      divider: true
    },
    {
      tooltip: 'slow queries',
      type: 'slowquery',
      icon: 'hourglass_full',
      help: true
    },
    {
      tooltip: 'log',
      type: 'log',
      icon: 'content_paste'
    },
    {
      tooltip: 'help',
      type: 'help',
      path: '/*',
      icon: 'help'
    },
    {
      divider: true
    },
    {
      tooltip: 'test',
      type: 'test',
      icon: 'bug_report',
      hidden: true
    },
    {
      caption: 'Virtual Services',
      type: 'custom',
      path: '/*',
      icon: 'group_work',
      help: true
    },
    {
      tooltip: 'Settings',
      type: 'settings',
      icon: 'settings'
    },
    {
      caption: 'API',
      type: 'api',
      icon: 'code',
      hidden: true,
      help: true
    }
  ];
  getHelpDefault() {
    const defaultp = _.find(this.pages, p => !!p.help && !!p.helpDefault);
    return defaultp ? defaultp.type : '';
  }
  getHelpPages() {
    return _.filter(this.pages, p => !!p.help);
  }
}



