import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CoverComponent} from './pages/cover/cover.component';
import {ScenarioComponent} from './pages/scenario/scenario.component';
import {DocumentsComponent} from './pages/documents/documents.component';
import {ConnectionsComponent} from './pages/connections/connections.component';
import {QueriesComponent} from './pages/queries/queries.component';
import {PreviewsComponent} from './pages/previews/previews.component';
import {CustomComponent} from './pages/custom/custom.component';
import {NoContentComponent} from './pages/nocontent/nocontent.component';
import {SettingsComponent} from './pages/settings/settings.component';
import {HelpComponent} from './pages/help/help.component';
import {CanDeactivateEditor} from './components/browser/browser.component';
import {SlowQueryComponent} from './pages/slow-query/slow-query.component';
import {LogComponent} from './pages/log/log.component';

const routes: Routes = [
  { path: '', redirectTo: '/cover', pathMatch: 'full' },
  { path: 'cover', component: CoverComponent },
  { path: 'scenario', component: ScenarioComponent },
  { path: 'documents/:id', component: DocumentsComponent },
  { path: 'connections/:id', component: ConnectionsComponent, canDeactivate: [CanDeactivateEditor] },
  { path: 'queries/:id', component: QueriesComponent, canDeactivate: [CanDeactivateEditor] },
  { path: 'previews/:id', component: PreviewsComponent },
  { path: 'custom/:id', component: CustomComponent, canDeactivate: [CanDeactivateEditor] },
  { path: 'slowquery', component: SlowQueryComponent },
  { path: 'log', component: LogComponent },
  { path: 'help/:id', component: HelpComponent },
  { path: 'settings', component: SettingsComponent },
  { path: '**', component: NoContentComponent }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes, { useHash: true }) ],
  exports: [ RouterModule ],
  providers: [ CanDeactivateEditor ]
})
export class AppRoutingModule { }



