import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { BrowserService } from '../../services/browser.service';
import { InteractionService } from '../../services/interaction.service';
import { UtilsService } from '../../services/utils.service';
import { SchemaCacheService } from '../../services/schema-cache.service';
import { CartService } from '../../services/cart.service';
import { DialogsService } from '../../services/dialogs.service';

import 'codemirror/mode/sql/sql';
import _ from 'lodash';
const EDITOR_TYPES = {
  get: 'get',
  add: 'add',
  update: 'update',
  'delete': 'delete'
};


@Component({
  selector: 'app-queries',
  templateUrl: './queries.component.html',
  styleUrls: ['./queries.component.css']
})
export class QueriesComponent implements OnInit, OnDestroy {
  @ViewChild('cmeditor') cmeditor: any;
  @ViewChild('cmeditor_add') cmeditor_add: any;
  @ViewChild('cmeditor_upd') cmeditor_upd: any;
  @ViewChild('cmeditor_del') cmeditor_del: any;
  constructor(public browser: BrowserService,
              private interaction: InteractionService,
              private u: UtilsService,
              private schema: SchemaCacheService,
              private cart: CartService,
              private dialogs: DialogsService) {
    const self = this;
    self.config = {
      lineNumbers: true,
      mode: 'javascript',
      theme: 'blackboard'
    };
    self.browser.register({
      type: self.interaction.types.queries,
      onSelect: () => {
        self.error = null;
        self.idle = false;
        self.result_str = '';
        self.result = null;
        self.browser.current._editor = EDITOR_TYPES.get;
        self.browser.current.dataentryOptions = self.browser.current.dataentryOptions || {};
        _.keys(EDITOR_TYPES).forEach((k) => self.browser.current.dataentryOptions[k] = self.browser.current.dataentryOptions[k] || '');
        self.dataEntryActive = !!_.find(_.keys(EDITOR_TYPES), (k) => !!this.browser.current.dataentryOptions[k]);
        self.checkConnection();
      },
      onLoaded: (docs) => {
        self.connections = _(docs)
          .filter(d => d._type === self.interaction.types.connections)
          .value();
        self.checkConnection();
      },
      template: {
        name: 'New Query',
        info: '',
        connection: '',
        query: '',
        columns: [],
        parameters: [],
        active: true
      }
    });
    self.header = {
      title: () => (self.browser.currentInfo || {}).name,
      isNew: () => (self.browser.current || {})._new,
      buttons: [{
        icon: 'add_circle_outline',
        tooltip: 'Add a new query',
        visible: () => !!self.browser.current,
        action: () => self.browser.newElement()
      }, {
        icon: 'flash_on',
        tooltip: 'Exec query',
        visible: () => !!self.browser.current,
        action: () => self.test()
      }, {
        icon: 'save',
        tooltip: 'Save modifies',
        visible: () => !!self.browser.current,
        action: () => self.browser.save('Query successfully updated!')
      }, {
        icon: 'add_shopping_cart',
        tooltip: 'Add to cart',
        visible: () => !!self.browser.current,
        disabled: () => cart.contains(self.browser.current),
        action: () => cart.add(self.browser.current)
      }, {
        icon: 'delete',
        tooltip: 'Remove query',
        color: 'warn',
        visible: () => !!self.browser.current,
        action: () => self.browser.delete('Delete current query?')
      }]
    };
  }
  ready = false;
  config = {};
  idle = false;
  header = {};
  connections = [];
  error = null;
  result = null;
  result_str = '';
  editor = null;
  dataEntryActive = false;
  static _getParameterIcon(p) {
    switch ((p || {}).type) {
      case 'lookup': return 'group_work';
      case 'list': return 'format_list_bulleted';
      case 'system': return 'settings';
      default: return 'radio_button_checked';
    }
  }
  test() {
    const self = this;
    if (self.idle) { return; }
    self.idle = true;
    self.error = null;
    self.result_str = '';
    self.result = null;
    self.u.idle = true;
    const st = (new Date()).getTime();
    self.interaction.getResult(self.browser.current, (err, data) => {
      const et = (new Date()).getTime();
      if (err) {
        self.error = self.u.getErrorMessage(err);
      }
      data = data || {};
      self.result_str = JSON.stringify(data, null, 2);
      self.result = data;
      self.result.stat = {
        elapsed: (et - st) + 'ms',
        rowCount: (data.rows || []).length,
        colCount: (data.columns || []).length
      };
      self.result.view = {
        rows: (data.rows || []).slice(0, 10),
        page: 0,
        pages: Math.floor((data.rows || []).length / 10) + 1,
        pageRows: 10
      };
      self.checkNavigator();
      self.browser.current.columns = data.columns || [];
      self.idle = false;
      self.u.idle = false;
      if (!err) {
        self.u.snack('Query executed: ' + self.result.stat.rowCount + ' records in ' + self.result.stat.elapsed);
      }
    });
  }
  checkConnection() {
    this.schema.connection((this.browser.current || {}).connection);
  }
  checkNavigator() {
    const pg = this.result.view.page;
    this.result.view.nextEnabled = (pg < this.result.view.pages - 1);
    this.result.view.prevEnabled = (pg > 0);
    this.result.view.state = (pg + 1) + ' of ' + this.result.view.pages;
  }
  navigate(prev?: boolean) {
    this.checkNavigator();
    this.result.view.page = prev ?
      (this.result.view.prevEnabled ? this.result.view.page - 1 : this.result.view.page) :
      (this.result.view.nextEnabled ? this.result.view.page + 1 : this.result.view.page);
    const pos = this.result.view.page * this.result.view.pageRows;
    this.result.view.rows = (this.result.rows || []).slice(pos, pos + this.result.view.pageRows);
    this.checkNavigator();
  }
  replaceSelection(sql, editor?) {
    editor = editor || this.cmeditor;
    if (!editor) { return; }
    const doc = editor.instance.getDoc();
    doc.replaceSelection(sql);
  }
  onInsert(page) {
    if (!page) { return; }
    const empty = (this.browser.current.query || '').trim() === '';
    const sqlname = this.schema.helper.getElementName(page);
    if (empty && (page.original.TABLE_TYPE || page.original.TABLE_LOCK)) {
      this.browser.current.query = this.schema.helper.generateSelect(page);
    } else if (empty && page.original.ROUTINE_TYPE === 'PROCEDURE') {
      this.browser.current.query = this.schema.helper.generateStored(page);
    } else if (empty && page.original.ROUTINE_TYPE) {
      this.browser.current.query = this.schema.helper.generateFunction(page);
    } else {
      this.replaceSelection(sqlname);
    }
    this.cmeditor.instance.focus();
  }
  currentEditor() {
    return (this.browser.current._editor === EDITOR_TYPES.add) ? this.cmeditor_add :
      ((this.browser.current._editor === EDITOR_TYPES.update) ? this.cmeditor_upd :
        ((this.browser.current._editor === EDITOR_TYPES.delete) ? this.cmeditor_del : null));
  }
  onInsertField(field) {
    const f = {name: field};
    const sql = this.schema.helper.getParameterSql(f);
    const editor = this.currentEditor();
    if (!editor) { return console.error('No codemirror editor available!'); }
    this.replaceSelection(sql, editor);
    editor.instance.focus();
  }
  getParameterIcon(p) {
    return QueriesComponent._getParameterIcon(p);
  }
  newParameter() {
    const name = this.u.getNewName(this.browser.current.parameters, 'New Parameter');
    const np = this.u.getNewParameter(name);
    this.browser.current.parameters.push(np);
  }
  isCloned(p) {
    return !!_.find(this.schema.paramStars, function (cp) {
      return cp.name === p.name && cp.type === p.type;
    });
  }
  star(p) {
    this.schema.star(p);
  }
  clearStarParams() {
    this.schema.starClear();
  }
  insert(p) {
    const sql = this.schema.helper.getParameterSql(p);
    const template = p.isOptional ? '{{IF ' + p.name + '}}\r\t' + sql + '\r{{ENDIF}}' : sql;
    this.replaceSelection(template);
  }
  insertStar(p) {
    const cp = _.clone(p);
    cp.id = this.u.guid();
    this.browser.current.parameters.push(cp);
  }
  edit(p) {
    this.dialogs.editParameter(p);
  }
  remove(p) {
    _.pull(this.browser.current.parameters, p);
  }
  setEditor(type?) {
    this.browser.current._editor = type || EDITOR_TYPES.get;
  }
  toggleDataEntry() {
    if (this.dataEntryActive) {
      this.dataEntryActive = false;
      _.keys(EDITOR_TYPES).forEach((k) => this.browser.current.dataentryOptions[k] = '');
    } else {
      this.dataEntryActive = true;
    }
  }
  private _generate() {
    const self = this;
    self.interaction.templatize({
      action: (self.browser.current || {})._editor,
      columns: (self.browser.current || {}).columns,
      connection: (self.browser.current || {}).connection
    }, (err, r) => {
      if ((r || {}).template) {
        self.browser.current.dataentryOptions[self.browser.current._editor] = r.template;
      }
    });
  }
  generate() {
    const self = this;
    const current = self.browser.current.dataentryOptions[self.browser.current._editor];
    if (current) {
      self.u.confirm({
        message: 'Current script will be overwritten, do you want to continue?',
        ok: 'Yes',
        cancel: 'No'
      }, resp => {
        (resp === 'ok') ? self._generate() : self.u.noop();
      });
    } else {
      self._generate();
    }
  }
  ngOnInit() {
    this.ready = true;
  }
  ngOnDestroy() {
    this.browser.deregister();
    this.schema.connection();
  }
}

