import { Component, OnInit, OnDestroy, ElementRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { fadeAnimation } from '../../app.animations';
import { InteractionService } from '../../services/interaction.service';
import { AppComponent } from '../../app.component';
import { UtilsService } from '../../services/utils.service';
import { UserSettingsService } from '../../services/user-settings.service';
import {CartService} from '../../services/cart.service';
import _ from 'lodash';

@Component({
  selector: 'app-scenario',
  templateUrl: './scenario.component.html',
  styleUrls: ['./scenario.component.css'],
  animations: [fadeAnimation]
})
export class ScenarioComponent implements OnInit, OnDestroy {
  @ViewChild('upload') uploadInput: ElementRef;
  uploading: Boolean = false;
  constructor(private interaction: InteractionService,
              private app: AppComponent,
              private u: UtilsService,
              public user: UserSettingsService,
              private cartS: CartService) {
    const self = this;
    self.header = {
      title: () => (self.current || {}).name,
      buttons: [{
        icon: 'star',
        tooltip: 'Set as the current scenario',
        color: 'warn',
        visible: () => !!self.current && !(self.current || {})._current,
        action: (e) => self.apply(self.current)
      }, {
        icon: 'save',
        tooltip: 'save changes',
        visible: () => !!self.current && (self.current || {})._current,
        action: (e) => self.update(self.current)
      }, {
        icon: 'refresh',
        tooltip: 'refresh',
        disabled: () => self.uploading,
        action: (e) => self.refresh()
      }, {
        icon: 'add_circle_outline',
        tooltip: 'Create new scenario',
        action: () => {
          const scenario = {
            name: this.u.getNewName(this.scenarios, 'New Scenario'),
            errors: [],
            auth: false
          };
          this.scenarios.push(scenario);
          this.select(scenario);
        }
      }, {
        divider: true
      }, {
        icon: 'cloud_upload',
        tooltip: 'upload scenario',
        visible: () => !!self.current,
        disabled: () => self.uploading,
        action: (e) => self.uploadInput.nativeElement.click()
      }, {
        icon: 'file_download',
        tooltip: 'download scenario',
        visible: () => !!self.current,
        action: (e) => self.interaction.download(self.current.folder)
      }]
    };
    self.interaction.events.subscribe((e) => {
      if (e.type === 'push') {
        self.refresh();
      }
    });
  }
  header = {};
  scenarios = null;
  current = null;
  current_str = '';
  items = [];
  currentSettings = '';
  private _reset() {
    const self = this;
    self.current = null;
    self.currentSettings = '';
  }
  refresh() {
    const self = this;
    self.interaction.getScenarios().subscribe(data => {
      self.scenarios = data;
      const cur = self.current ?
        _.find(data, s => s.folder === self.current.folder) :
        _.find(data, s => s._current);
      self.select(cur);
    }, err => {
      self.u.error(err);
    });
  }
  apply(scenario) {
    const self = this;
    self._reset();
    self.app.apply(scenario, () => {
      self.refresh();
      self.u.snack('Scenario successfully changed!');
    });
  }
  update(scenario) {
    const self = this;
    self._reset();
    self.app.update(scenario, () => {
      self.refresh();
      self.u.snack('Scenario successfully updated!');
    });
  }
  select(scenario) {
    this.current = scenario;
    this.currentSettings = JSON.stringify((scenario || {}).settings || {}, null, 2);
    this.cartS.scenario = scenario;
    this.current_str = JSON.stringify(scenario, null, 2);
    this.items = [];
    _.keys(this.current || {}).forEach(pn => {
      if (_.isArray(this.current[pn]) && this.current[pn].length) {
        this.items.push({name: pn, count: this.current[pn].length});
      }
    });
  }
  updateSettings() {
    try {
      this.current.settings = JSON.parse(this.currentSettings);
      this.currentSettings = JSON.stringify(this.current.settings, null, 2);
    } catch (err) {
      console.error(err);
    }
  }
  clearFile() {
    this.uploadInput.nativeElement.value = '';
    this.uploading = false;
  }
  onFileChange(e) {
    e.preventDefault();
    e.stopPropagation();
    if (e.target.files && e.target.files.length > 0) {
      const self = this;
      const file = e.target.files[0];
      if (!/(.*).zip/i.test(file.name)) {
        self.clearFile();
        self.u.error('Scenario must be a zip file!');
      } else {
        self.uploading = true;
        const data = new FormData();
        data.append('file', file);
        self.interaction.upload(data, () => {
          self.clearFile();
          self.refresh();
        });
      }
    }
  }
  ngOnInit() {
    this.cartS.scenario = null;
    this.refresh();
  }
  ngOnDestroy() {
  }
}



