import { Component, OnInit, OnDestroy } from '@angular/core';
import { BrowserService } from '../../services/browser.service';
import { InteractionService } from '../../services/interaction.service';
import { UtilsService } from '../../services/utils.service';
import { CartService } from '../../services/cart.service';

@Component({
  selector: 'app-documents',
  templateUrl: './documents.component.html'
})
export class DocumentsComponent implements OnInit, OnDestroy {

  constructor(public browser: BrowserService,
              private interaction: InteractionService,
              private u: UtilsService,
              private cart: CartService) {
    const self = this;
    self.browser.register({
      type: 'documents',
      description: (d) => self.browser.options.showAll ? d._type : ''
    });
    self.header = {
      title: () => (self.browser.currentInfo || {}).name,
      buttons: [{
        icon: 'gps_off',
        tooltip: 'All document types',
        color: 'warn',
        selected: () => self.browser.options.showAll,
        action: () => {
          self.browser.options.showAll = !self.browser.options.showAll;
          self.browser.refresh();
        }
      }, {
        icon: 'save',
        tooltip: 'Save modifies',
        visible: () => !!self.browser.current,
        action: () => {
          self.u.confirm({
            message: 'Save document modifies?',
            ok: 'Ok',
            cancel: 'Cancel'
          }, resp => {
            if (resp === 'ok') {
              self.browser.current = JSON.parse(self.browser.current_str);
              self.browser.save('Document successfully updated!');
            }
          });
        }
      }, {
        icon: 'add_shopping_cart',
        tooltip: 'Add to cart',
        visible: () => !!self.browser.current,
        disabled: () => cart.contains(self.browser.current),
        action: () => cart.add(self.browser.current)
      }, {
        icon: 'delete',
        tooltip: 'Remove document',
        color: 'warn',
        disabled: () => !self.browser.current,
        action: () => self.browser.delete()
      }]
    };
  }
  header = {};
  ngOnInit() {
  }
  ngOnDestroy() {
    this.browser.deregister();
  }
}



