import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import {BrowserService} from '../../services/browser.service';


@Component({
  selector: 'app-fields-browser',
  templateUrl: './fields-browser.component.html'
})
export class FieldsBrowserComponent implements OnInit {
  @Output() insert: EventEmitter<string> = new EventEmitter<string>();
  constructor(public browser: BrowserService) {}
  ngOnInit() {
  }
  notifyAdd(field) {
    this.insert.emit(field.name);
  }
}



