import { Component, OnInit, Input, Output,  EventEmitter } from '@angular/core';

@Component({
  selector: 'app-help-browser',
  templateUrl: './help-browser.component.html'
})
export class HelpBrowserComponent implements OnInit {
  @Input('map') map: object;
  @Output() goTo = new EventEmitter<string>();
  constructor() { }
  goto(target) {
    this.goTo.emit(target);
  }
  ngOnInit() {}
}



