import { Component, OnInit, OnDestroy } from '@angular/core';
import { BrowserService } from '../../services/browser.service';
import { InteractionService } from '../../services/interaction.service';
import { UtilsService } from '../../services/utils.service';
import { CartService } from '../../services/cart.service';
import _ from 'lodash';

@Component({
  selector: 'app-connections',
  templateUrl: './connections.component.html',
  styleUrls: ['./connections.component.css']
})
export class ConnectionsComponent implements OnInit, OnDestroy {

  constructor(public browser: BrowserService,
              private interaction: InteractionService,
              private u: UtilsService,
              private cart: CartService) {
    const self = this;
    self.browser.register({
      type: self.interaction.types.connections,
      onSelect: () => self.error = null,
      template: {
        name: 'New Connection',
        info: '',
        user: '',
        password: '',
        server: '',
        database: '',
        port: 1433,
        provider: 'sqlserver',
        active: true
      }
    });
    self.header = {
      title: () => (self.browser.currentInfo || {}).name,
      isNew: () => (self.browser.current || {})._new,
      buttons: [{
        icon: 'add_circle_outline',
        tooltip: 'Add a new db connection',
        visible: () => !!self.browser.current,
        action: (e) => self.browser.newElement()
      }, {
        icon: 'save',
        tooltip: 'Save modifies',
        visible: () => !!self.browser.current,
        action: () => self.browser.save('Connection successfully updated!')
      }, {
        icon: 'add_shopping_cart',
        tooltip: 'Add to cart',
        visible: () => !!self.browser.current,
        disabled: () => cart.contains(self.browser.current),
        action: () => cart.add(self.browser.current)
      }, {
        icon: 'delete',
        tooltip: 'Remove connection',
        color: 'warn',
        visible: () => !!self.browser.current,
        action: (e) => self.browser.delete('Delete current connection?')
      }]
    };
    self.interaction.getProviders().subscribe(providers => {
      if (_.keys(providers || {}).length) {
        self.providers = _.filter(_.map(_.keys(providers), k => providers[k]), p => p.active);
      }
    });
  }
  header = {};
  providers = [
    {name: 'SQL Server', code: 'sqlserver', defaultPort: 1433}
  ];
  error = null;
  test() {
    const self = this;
    self.interaction.testConnection(self.browser.current, (err) => {
      self.error = err ? self.u.getErrorMessage(err) : null;
      self.u.snack(self.error || 'Connection successfully tested!');
    });
  }
  checkType() {
    this.browser.current.server = '';
    this.browser.current.user = '';
    this.browser.current.password = '';
    this.browser.current.database = '';
    const choice = _.find(this.providers, (p) => p.code === this.browser.current.provider);
    if (choice) {
      this.browser.current.port = choice.defaultPort;
    }
  }
  ngOnInit() {
  }
  ngOnDestroy() {
    this.browser.deregister();
  }
}
