import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from '../../../environments/environment';
import { fadeAnimation } from '../../app.animations';
import { AppComponent } from '../../app.component';

@Component({
  selector: 'app-cover',
  templateUrl: './cover.component.html',
  animations: [fadeAnimation]
})

export class CoverComponent implements OnInit {
  constructor(private router: Router,
              private app: AppComponent) {}
  title = 'Echo Service';
  github = '' + environment.github;
  footer = 'echo-service v.?';
  message() {
    const app = this.app || {};
    const scenario = (app['info'] || {})['scenario'] || {};
    this.footer = 'echo-service v.' + ((app['info'] || {})['ver'] || '?');
    return app['loading'] ? 'loading scenario...' : (scenario['name'] || 'unknown scenario');
  }
  go() {
    if (!(this.app || {})['loading']) {
      const route = (this.app || {})['error'] ? '/settings' : '/scenario';
      this.router.navigate([route]);
    }
  }
  ngOnInit() {
    this.app.refresh();
  }
}



