import { Injectable, EventEmitter } from '@angular/core';
import { InteractionService } from './interaction.service';
import { UtilsService } from './utils.service';
import _ from 'lodash';

export class SchemaCacheEvent {
  constructor() {}
}

export class SchemaPage {
  title = '';
  type = '';
  pages: SchemaPage[] = [];
  value = '';
  icon = '';
  postfix = '';
  sortField = 'title';
  priority = 0;
  prev = null;
  original: any = null;
  constructor(info: any) {
    _.assign(this, info);
  }
  addPage(page) {
    page.prev = this;
    this.pages.push(page);
  }
}

export class SchemaHelper {
  constructor(private u: UtilsService) { }
  provider = '';
  providers = {
    mysql: 'mysql',
    oracle: 'oracle',
    ibmdb2: 'ibmdb2',
    sqlserver: 'sqlserver'
  };
  private static _isTableOrRoutine(ele) {
    return ele.original.TABLE_TYPE || ele.original.ROUTINE_TYPE;
  }
  private _getFix(pre?: boolean, field?: boolean) {
    const self = this;
    switch (self.provider) {
      case self.providers.mysql:
      case self.providers.oracle:
      case self.providers.ibmdb2:
        return field ? '' : '';
      case self.providers.sqlserver:
      default: return pre ? '[' : ']';
    }
  }
  getElementName(ele: SchemaPage) {
    const self = this;
    if (SchemaHelper._isTableOrRoutine(ele)) {
      const schema = ele.original.TABLE_SCHEMA || ele.original.SPECIFIC_SCHEMA;
      return ((schema || '').toLowerCase() !== 'dbo') ? self._getFix(true) + schema + self._getFix() +
        '.' + self._getFix(true) + ele.value + self._getFix() : self._getFix(true) + ele.value + self._getFix();
    } else {
      return self._getFix(true, true) + ele.value + self._getFix(false, true);
    }
  }
  generateSelect(ele: SchemaPage, limit?: number) {
    const self = this;
    if (_.isUndefined(limit)) { limit = 100; }

    let fields = '';
    ele.pages.forEach((f) => {
      if (fields) { fields += ','; }
      fields += '\n\t' + self.getElementName(f);
    });

    switch (self.provider) {
      case self.providers.mysql:
        return 'SELECT' + fields + '\nFROM\n\t' + self.getElementName(ele) + (limit > 0 ? '\nLIMIT ' + limit : '');
      case self.providers.oracle:
        return 'SELECT' + fields + '\nFROM\n\t' + self.getElementName(ele);
      case self.providers.ibmdb2:
        return 'SELECT' + fields + '\nFROM\n\t' + self.getElementName(ele) + (limit > 0 ? '\nFETCH FIRST ' + limit + ' ROWS ONLY' : '');
      case self.providers.sqlserver:
      default:
        return 'SELECT\n\t' + (limit > 0 ? 'TOP ' + limit : '') + fields + '\nFROM\n\t' + self.getElementName(ele);
    }
  }
  generateStored(ele: SchemaPage) {
    let params_declare = '';
    let params_set = '';
    let params_exec = '';
    let params_out = '';
    ele.pages.forEach((p) => {
      if (p.original.PARAMETER_MODE && p.original.PARAMETER_MODE.substr(0, 2) === 'IN') {
        if (params_declare) {
          params_declare += ',\n\t';
        } else {
          params_declare += 'DECLARE\n\t';
        }
        const len = (p.original.CHARACTER_MAXIMUM_LENGTH && parseInt(p.original.CHARACTER_MAXIMUM_LENGTH + '', 10) !== 0) ?
          '(' + p.original.CHARACTER_MAXIMUM_LENGTH + ')' : '';
        params_declare += p.original.PARAMETER_NAME + ' ' + p.original.DATA_TYPE + len;
        params_set += 'SET ' + p.original.PARAMETER_NAME + ' = {VALUE}\n';
        if (params_exec) { params_exec += ', '; }
        params_exec += p.original.PARAMETER_NAME;
      } else if (p.original.PARAMETER_MODE) {
        if (params_declare) {
          params_declare += ',\n\t';
        } else {
          params_declare += 'DECLARE\n\t';
        }
        const len = (p.original.CHARACTER_MAXIMUM_LENGTH && parseInt(p.original.CHARACTER_MAXIMUM_LENGTH + '', 10) !== 0) ?
          '(' + p.original.CHARACTER_MAXIMUM_LENGTH + ')' : '';
        params_declare += p.original.PARAMETER_NAME + ' ' + p.original.DATA_TYPE + len;
        params_out = p.original.PARAMETER_NAME + ' = ';
      }
    });
    if (params_declare) {
      params_declare += ';\n' + params_set + '\n';
    }
    if (params_exec) {
      params_exec += ';';
    }
    if (!params_out) {
      params_declare += '\nDECLARE @RC int;\n\n';
      params_out = '@RC = ';
    }
    return params_declare + 'EXEC ' + params_out + '[' + ele.original.ROUTINE_SCHEMA + '].[' +
      ele.original.ROUTINE_NAME + '] ' + params_exec;
  }
  generateFunction(ele: SchemaPage) {
    let params_declare = '';
    let params_exec = '';
    let params_out = '';
    let params_set = '';

    const outtable = ele.original.DATA_TYPE === 'TABLE';
    ele.pages.forEach((p) => {
      if (p.original.PARAMETER_MODE && p.original.PARAMETER_MODE.substr(0, 2) === 'IN') {
        if (params_declare) {
          params_declare += ',\n\t';
        } else {
          params_declare += 'DECLARE\n\t';
        }
        const len = (p.original.CHARACTER_MAXIMUM_LENGTH && parseInt(p.original.CHARACTER_MAXIMUM_LENGTH, 10) !== 0) ?
          '(' + p.original.CHARACTER_MAXIMUM_LENGTH + ')' : '';
        params_declare += p.original.PARAMETER_NAME + ' ' + p.original.DATA_TYPE + len;
        if (params_exec) { params_exec += ', '; }
        params_exec += p.original.PARAMETER_NAME + (outtable ? '' : '={VALUE}');
        params_set += 'SET ' + p.original.PARAMETER_NAME + ' = {VALUE}\n';
      } else if (p.original.PARAMETER_MODE) {
        if (params_declare) {
          params_declare += ',\n\t';
        } else {
          params_declare += 'DECLARE\n\t';
        }
        const len = (p.original.CHARACTER_MAXIMUM_LENGTH && parseInt(p.original.CHARACTER_MAXIMUM_LENGTH, 10) !== 0) ?
          '(' + p.original.CHARACTER_MAXIMUM_LENGTH + ')' : '';
        params_declare += '@RES ' + p.original.DATA_TYPE + len;
        params_out = '@RES = ';
      }
    });
    if (params_exec && !outtable) {
      params_exec += ';';
    }
    if (params_out) {
      params_exec += '\nSELECT @RES AS RESULT';
    }

    return outtable ?
      params_declare + '\n\n' + params_set + '\n\nSELECT * FROM [' + ele.original.ROUTINE_SCHEMA + '].[' +
      ele.original.ROUTINE_NAME + '](' + params_exec + ')' :
      params_declare + '\n\nEXEC ' + params_out + '[' + ele.original.ROUTINE_SCHEMA + '].[' +
      ele.original.ROUTINE_NAME + '] ' + params_exec;
  }
  getSqlDate(name) {
    const self = this;
    switch (self.provider) {
      case self.providers.mysql:
        return 'str_to_date(\'{{=' + name + '}}\', \'%Y-%m-%d %H:%i:%s\')';
      case self.providers.oracle:
      case self.providers.ibmdb2:
        return 'TO_DATE(\'{{=' + name + '}}\',\'YYYY-MM-DD"T"HH24:MI:SS\')';
      case self.providers.sqlserver:
      default:
        return 'CONVERT(datetime,\'{{=' + name + '}}\', 126)';
    }
  }
  getParameterSql(p: any) {
    switch (this.u.getDataType(p.dataType)) {
      case 'date':
        return this.getSqlDate(p.name);
      case 'string':
        return '\'{{=' + p.name + '}}\'';
      case 'bool':
      case 'number':
      default:
        return '{{=' + p.name + '}}';
    }
  }
}



@Injectable()
export class SchemaCacheService {

  constructor(private interaction: InteractionService,
              private u: UtilsService) { }
  schemas = {};
  paramStars = [];
  idle = false;
  cnnId = null;
  schema = null;
  error = null;
  helper = new SchemaHelper(this.u);
  events: EventEmitter<SchemaCacheEvent> = new EventEmitter();
  private findPage(t: string, list1: SchemaPage[], list2: SchemaPage[]) {
    const p = _.find(list1, p1 => p1.title === t);
    if (p) { return p; }
    return _.find(list2, p2 => p2.title === t);
  }
  private build() {
    const self = this;
    const main: SchemaPage = new SchemaPage({
      title: (self.schema || {}).title || 'none',
      sortField: 'priority'
    });
    if (!self.schema) { return main; }

    const pT = new SchemaPage({title: 'Tables', type: 'tables', icon: 'grid_on', priority: 0});
    main.addPage(pT);

    const pV = new SchemaPage({title: 'Viewes', type: 'views', icon: 'remove_red_eye', priority: 1});
    main.addPage(pV);

    // TABELLE & VISTE
    if (_.isArray(self.schema.tables)) {
      self.schema.tables.forEach(t => {
        const p = new SchemaPage({
          title: t.TABLE_NAME,
          type: 'table',
          icon: (t.TABLE_TYPE === 'VIEW') ? 'remove_red_eye' : 'grid_on',
          value: t.TABLE_NAME,
          original: t
        });
        if (t.TABLE_TYPE === 'VIEW') {
          pV.addPage(p);
        } else {
          pT.addPage(p);
        }
      });
    }
    if (_.isArray(self.schema.views)) {
      self.schema.views.forEach((t) => {
        const p = new SchemaPage({
          title: t.TABLE_NAME,
          type: 'view',
          icon: 'remove_red_eye',
          value: t.TABLE_NAME,
          original: t
        });
        pV.addPage(p);
      });
    }

    // COLONNE x TABELLE & VISTE
    if (_.isArray(self.schema.columns)) {
      self.schema.columns.forEach((c) => {
        const p = self.findPage(c.TABLE_NAME, pT.pages, pV.pages);
        const dp = new SchemaPage({
          title: c.COLUMN_NAME + ' (' + c.DATA_TYPE + ')',
          icon: 'filter_center_focus',
          value: c.COLUMN_NAME,
          postfix: c.DATA_TYPE,
          original: c
        });
        if (p) { p.addPage(dp); }
      });
    }

    const pS = new SchemaPage({title: 'Storeds', type: 'storeds', icon: 'slideshow', priority: 2});
    main.addPage(pS);

    const pF = new SchemaPage({title: 'Functions', type: 'functions', icon: 'slideshow', priority: 3});
    main.addPage(pF);

    // STOREDS & FUNCTIONS
    if (_.isArray(self.schema.routines)) {
      self.schema.routines.forEach((r) => {
        const p = new SchemaPage({
          title: r.ROUTINE_NAME,
          type: (r.ROUTINE_TYPE === 'PROCEDURE') ? 'procedure' : 'function',
          icon: 'slideshow',
          value: r.ROUTINE_NAME,
          original: r,
          popup: r.ROUTINE_DEFINITION
        });
        if (r.ROUTINE_TYPE === 'PROCEDURE') {
          pS.addPage(p);
        } else {
          pF.addPage(p);
        }
      });
    }
    // COLONNE x STOREDS & FUNCTIONS
    if (_.isArray(self.schema.routineColumns)) {
      self.schema.routineColumns.forEach((c) => {
        const p = self.findPage(c.TABLE_NAME, pS.pages, pF.pages);
        const dp = new SchemaPage({
          title: c.COLUMN_NAME + ' (' + c.DATA_TYPE + ')',
          icon: 'filter_center_focus',
          value: c.COLUMN_NAME,
          postfix: c.DATA_TYPE,
          original: c
        });
        if (p) { p.addPage(dp); }
      });
    }
    if (_.isArray(self.schema.parameters)) {
      self.schema.parameters.forEach((p) => {
        const r = self.findPage(p.SPECIFIC_NAME, pS.pages, pF.pages);
        const dp = new SchemaPage({
          title: p.PARAMETER_NAME ? p.PARAMETER_NAME : 'OUT',
          icon: 'filter_tilt_shift',
          value: p.PARAMETER_NAME,
          postfix: p.DATA_TYPE,
          original: p
        });
        if (r) { r.addPage(dp); }
      });
    }
    return main;
  }
  getInfoPage(page: SchemaPage) {
    const info = new SchemaPage({
      prev: page.prev,
      title: page.title,
      pages: []
    });
    _.keys(page.original).forEach(pn => {
      info.pages.push(new SchemaPage({ title: pn + ': ' + page.original[pn] }));
    });
    return info;
  }
  getSchema(cnnId: string) {
    const self = this;
    return new Promise((resolve, reject) => {
      if (!self.schemas[cnnId]) {
        self.interaction.getSchema(cnnId, (err, schema) => {
          if (err) { return reject(err); }
          self.schemas[cnnId] = schema;
          resolve(self.schemas[cnnId]);
        });
      } else {
        resolve(self.schemas[cnnId]);
      }
    });
  }
  private checkSchema(schema?: any) {
    this.schema = schema;
    if (this.schema && !this.schema.model) {
      this.schema.model = this.build();
    }
  }
  flush(cnnId) {
    this.u.empty(this.schemas, cnnId);
  }
  refresh() {
    if (!this.cnnId) { return; }
    this.flush(this.cnnId);
    this.connection(this.cnnId);
  }
  connection(cnnId?: string) {
    // TODO imposta la connessione corrente
    const self = this;
    if (self.cnnId === cnnId) { return; }
    self.idle = true;
    self.error = null;
    self.cnnId = cnnId;
    if (!cnnId) {
      self.schema = null;
      self.checkSchema();
      self.idle = false;
    } else {
      this.getSchema(this.cnnId)
        .then(schema => {
          self.error = null;
          self.checkSchema(schema);
          self.idle = false;
          self.events.emit(new SchemaCacheEvent());
        }, err => {
          console.error(err);
          self.error = self.u.getErrorMessage(err);
          self.checkSchema();
          self.idle = false;
          self.events.emit(new SchemaCacheEvent());
        });
    }
  }
  star(p) {
    if (!_.find(this.paramStars, sp => sp.id === p.id)) {
      this.paramStars.push(_.clone(p));
    }
  }
  starClear() {
    this.paramStars.splice(0);
  }
}



