import { Component, ViewChild, OnInit, OnDestroy, Renderer2 } from '@angular/core';
import { BrowserService } from '../../services/browser.service';
import { InteractionService } from '../../services/interaction.service';
import { CartService } from '../../services/cart.service';

@Component({
  selector: 'app-previews',
  templateUrl: './previews.component.html',
  styleUrls: ['./previews.component.css']
})
export class PreviewsComponent implements OnInit, OnDestroy {
  @ViewChild('preview') preview: any;
  constructor(public browser: BrowserService,
              private interaction: InteractionService,
              private renderer: Renderer2,
              private cart: CartService) {
    const self = this;
    self.browser.register({
      type: 'previews',
      namePrp: '_id',
      onSelect: doc => {
        const img = renderer.createElement('img');
        renderer.setAttribute(img, 'src', doc.data);
        const cc = this.preview.nativeElement.firstChild;
        if (cc) { this.preview.nativeElement.removeChild(cc); }
        renderer.appendChild(this.preview.nativeElement, img);
      }
    });
    self.header = {
      title: () => (self.browser.currentInfo || {})._id,
      buttons: [{
        icon: 'delete_forever',
        tooltip: 'Remove not used',
        disabled: () => true,
        action: () => self.interaction.deleteNotUsedPreviews()
      }, {
        icon: 'add_shopping_cart',
        tooltip: 'Add to cart',
        visible: () => !!self.browser.current,
        disabled: () => cart.contains(self.browser.current),
        action: () => cart.add(self.browser.current)
      }, {
        icon: 'delete',
        tooltip: 'Remove preview',
        color: 'warn',
        disabled: () => !self.browser.current,
        action: () => self.browser.delete('Delete current preview?')
      }]
    };
  }
  header = {};
  ngOnInit() {
  }
  ngOnDestroy() {
    this.browser.deregister();
  }
}



