import { Component, OnInit, OnDestroy, ViewChild, ViewContainerRef, ComponentRef, ComponentFactoryResolver } from '@angular/core';
import { UtilsService } from '../../services/utils.service';
import { Router, NavigationEnd } from '@angular/router';
import { AppPagesService } from '../../services/app-pages.service';
import { ScrollToService, ScrollToConfigOptions } from '@nicky-lenaers/ngx-scroll-to';
import _ from 'lodash';


@Component({
  selector: 'app-help',
  templateUrl: './help.component.html'
})
export class HelpComponent implements OnInit, OnDestroy {
  @ViewChild('target', { read: ViewContainerRef }) target: any;
  public cmpRef: ComponentRef<any>;
  private _initialized: Boolean = false;
  _pageId: string;
  _defaultPageId = '';
  constructor(private router: Router,
              private app: AppPagesService,
              private u: UtilsService,
              private resolver: ComponentFactoryResolver,
              private scroll: ScrollToService) {
    const self = this;
    self.header = {
      title: () => (self.current || {}).caption || (self.current || {}).tooltip,
      buttons: []
    };
    self._defaultPageId = app.getHelpDefault();
    self.helpItems = app.getHelpPages();
    router.events.subscribe((event) => {
      const nurl = event['url'];
      if (event instanceof NavigationEnd) {
        self.u.parseUrlId(nurl, (url, id) => {
          if (url === '/help' && self._pageId !== id) {
            self.load(id);
          }
        });
      }
    });
  }
  header = {};
  helpItems = [];
  current = null;
  searchTerm = '';
  private _clear() {
    const self = this;
    self.current = null;
    if (self.cmpRef) {
      self.cmpRef.destroy();
      self.cmpRef = null;
    }
  }
  ngOnInit() {
    const self = this;
    self._initialized = true;
    self.load(self._pageId);
  }
  ngOnDestroy() {
    this._clear();
  }
  goto(target, duration: number = 650) {
    const config: ScrollToConfigOptions = {target: target, offset: -64, duration: duration};
    this.scroll.scrollTo(config);
  }
  load(id) {
    const self = this;
    self._pageId = id;
    self._clear();
    if (id && self._initialized) {
      if (id === '*') { id = self._defaultPageId; }
      this.current = _.find(this.app.pages, p => p.type === id);
      const name = self.u.upperFirst(id) + 'HelpComponent';
      const factories = Array.from(this.resolver['_factories'].keys());
      const factoryClass = _.find(factories, (f) => f.name === name);
      if (factoryClass) {
        const factory = this.resolver.resolveComponentFactory(factoryClass);
        this.cmpRef = this.target.createComponent(factory);
        this.goto('__top__', 0);
      }
    }
  }
  refresh() {

  }
  clearSearch() {
    this.searchTerm = '';
    this.refresh();
  }
  select(i) {
    this.router.navigate(['help', i.type]);
  }
}




