import {Component, OnInit, Input, Injectable} from '@angular/core';
import { BrowserService } from '../../services/browser.service';
import _ from 'lodash';

@Component({
  selector: 'app-browser',
  templateUrl: './browser.component.html'
})
export class BrowserComponent implements OnInit {
  @Input() searchTerm: string;
  constructor(private browser: BrowserService) {
    const self = this;
    self.browser.onChanged = () => self.refresh();
  }
  documents = [];
  clear() {
    this.searchTerm = '';
    this.refresh();
  }
  refresh() {
    const rgx = this.searchTerm ? new RegExp(this.searchTerm, 'i') : null;
    this.documents = rgx ? _.filter(this.browser.documents,
        d => rgx.test(d[this.browser.options.namePrp || 'name'])) : this.browser.documents;
  }
  ngOnInit() {
  }
}


import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { UtilsService } from '../../services/utils.service';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class CanDeactivateEditor implements CanDeactivate<BrowserComponent> {
  constructor(private browser: BrowserService,
              private u: UtilsService) {}

  canDeactivate(
    component: BrowserComponent,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState: RouterStateSnapshot
  ): Observable<boolean>|Promise<boolean>|boolean {
    const self = this;
    const source = self.u.parseUrlId(currentState.url);
    const target = self.u.parseUrlId(nextState.url);
    if (source === target) { return true; }
    if (!_.find(self.browser.documents, d => d._new)) { return true; }
    return new Promise<boolean>((resolve, reject) => {
      self.u.confirm({
        message: 'Some documents have been created.\nDo you want to save them?',
        ok: 'Yes',
        no: 'No',
        cancel: 'Cancel'
      }, (resp) => {
        switch (resp) {
          case 'ok':
            self.browser.saveAll().then(() => resolve(true));
            break;
          case 'no':
            resolve(true);
            break;
          case 'cancel':
          default:
            resolve(false);
        }
      });
    });
  }
}



