import { Component, OnInit } from '@angular/core';
import { InteractionService } from '../../../services/interaction.service';
import {UtilsService} from '../../../services/utils.service';
import _ from 'lodash';


@Component({
  selector: 'app-api-help',
  templateUrl: './api-help.component.html'
})
export class ApiHelpComponent implements OnInit {
  constructor(private interaction: InteractionService,
              private u: UtilsService) { }
  apis = [];
  map = [];
  apiTest: any = {show: false};
  openTest(route) {
    if (!this.apiTest.running) {
      this.apiTest.path = route.route;
      this.apiTest.verb = route.verb;
      this.apiTest.body = null;
      this.apiTest.response = null;
    }
    this.apiTest.show = true;
  }
  private _object(o) {
    const po = [];
    _.keys(o).forEach(k => po.push({name: k, type: o[k].type, description: o[k].description}));
    return po;
  }
  ngOnInit() {
    const self = this;
    self.map = [];
    self.interaction.api(apis => {
      (apis || []).forEach(api => {
        self.map.push({title: api.description});
        (api.routes || []).forEach(r => {
          r._response = _.isObject((r.response || {}).object) ? self._object(r.response.object) : null;
          r._response_str = _.isString(r.response) ? r.response : (r.response || {}).description;
          r._body = _.isObject(r.body) ? self._object(r.body) : null;
          r._body_str = _.isString(r.body) ? r.body : null;
          r._id = this.u.getHtmlId(r.description);
          self.map.push({id: r._id, desc: r.description});
        });
      });
     self.apis = apis;
    });
  }
}



