import {Component, OnInit, ViewChild} from '@angular/core';
import { fadeAnimation } from './app.animations';
import { Router, NavigationEnd } from '@angular/router';
import { AppPagesService } from './services/app-pages.service';
import { InteractionService } from './services/interaction.service';
import { UtilsService } from './services/utils.service';
import { CartService } from './services/cart.service';
import _ from 'lodash';

const route_cache = {
  backs: ['/settings'],
  last: null
};

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [fadeAnimation]
})

export class AppComponent implements OnInit {
  @ViewChild('cart') cart: any;
  constructor(private router: Router,
              private app: AppPagesService,
              private interaction: InteractionService,
              private u: UtilsService,
              public cartS: CartService) {
    const self = this;
    self.loading = true;
    router.events.subscribe((event) => {
      const url = event['url'];
      if (event instanceof NavigationEnd) {
        this.back = (route_cache.backs.indexOf(url) > -1) ? route_cache.last : null;
        route_cache.last = url;
        const target = _.find(app.pages, p => (url || '').indexOf('/' + p.type) === 0) || {};
        const current = _.find(app.pages, p => p.selected) || {};
        if (target !== current) {
          current.selected = false;
          target.selected = true;
        }
        this.cart.opened = false;
      }
    });
  }
  info = {};
  error = null;
  back = '';
  loading = false;
  left_pages = _.filter(this.app.pages, p => !p.hidden && p.left);
  right_pages = _.filter(this.app.pages, p => !p.hidden && !p.left);
  refresh() {
    const self = this;
    self.loading = true;
    self.error = null;
    self.interaction.getInfo().subscribe(data => {
      self.info = data;
      self.loading = false;
    }, err => {
      self.info = {};
      console.error(err);
      self.error = self.u.getErrorMessage(err);
      self.loading = false;
    });
  }
  ngOnInit() {
    this.refresh();
  }
  apply(scenario, cb) {
    const self = this;
    self.interaction.setScenario(scenario, () => {
      self.refresh();
      cb();
    });
  }
  update(scenario, cb) {
    const self = this;
    self.interaction.saveScenario(scenario, () => {
      self.refresh();
      cb();
    });
  }
  select(page) {
    const current = _.find(this.app.pages, p => p.selected);
    const self = this;
    if (current !== page) {
      self.router.navigate(['/' + page.type + (page.path || '')]);
    }
  }
  getRouterOutletState(outlet) {
    return outlet.isActivated ? outlet.activatedRoute : '';
  }
  isShowToolbar() {
    return this.router.url !== '/cover';
  }
  exit() {
    this.router.navigate(['/']);
  }
  goBack() {
    if (this.back) {
      this.u.parseUrlId(this.back, (url, id) => {
        const target = id ? [url, id] : [url];
        this.router.navigate(target);
      });
    }
  }
}



