import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-slow-query',
  templateUrl: './slow-query.component.html',
  styleUrls: ['./slow-query.component.css']
})
export class SlowQueryComponent implements OnInit {
  @ViewChild('invaders') invaders: ElementRef;
  constructor() {}
  game = null;
  running = false;
  player = null;
  aliens = null;
  bullets = null;
  bulletTime = 0;
  cursors = null;
  fireButton = null;
  explosions = null;
  starfield = null;
  score = 0;
  scoreString = '';
  scoreText = null;
  lives = null;
  enemyBullet = null;
  firingTimer = 0;
  stateText = null;
  livingEnemies = [];
  enemyBullets = null;
  preload() {
    this.game.load.image('bullet', 'assets/invaders/bullet.png');
    this.game.load.image('enemyBullet', 'assets/invaders/enemy-bullet.png');
    this.game.load.spritesheet('invader', 'assets/invaders/invader32x32x4.png', 32, 32);
    this.game.load.image('ship', 'assets/invaders/Mastiff-rgb.png');
    this.game.load.spritesheet('kaboom', 'assets/invaders/explode.png', 128, 128);
    this.game.load.image('starfield', 'assets/invaders/starfield.png');
    this.game.load.image('background', 'assets/invaders/background2.png');
  }
  setupInvader(invader) {
    invader.anchor.x = 0.5;
    invader.anchor.y = 0.5;
    invader.animations.add('kaboom');
  }
  descend() {
    this.aliens.y += 10;
  }
  createAliens () {
    for (let y = 0; y < 4; y++) {
      for (let x = 0; x < 10; x++) {
        const alien = this.aliens.create(x * 48, y * 50, 'invader');
        alien.anchor.setTo(0.5, 0.5);
        alien.animations.add('fly', [ 0, 1, 2, 3 ], 20, true);
        alien.play('fly');
        alien.body.moves = false;
      }
    }
    this.aliens.x = 100;
    this.aliens.y = 50;

    // All this does is basically start the invaders moving. Notice we're moving the Group they belong to,
    // rather than the invaders directly.
    const tween = this.game.add.tween(this.aliens).to( { x: 200 }, 2000, Phaser.Easing.Linear.None, true, 0, 1000, true);

    //  When the tween loops it calls descend
    tween.onLoop.add(this.descend, this);
  }
  create() {
    this.game.physics.startSystem(Phaser.Physics.ARCADE);

    //  The scrolling starfield background
    this.starfield = this.game.add.tileSprite(0, 0, 800, 600, 'starfield');

    //  Our bullet group
    this.bullets = this.game.add.group();
    this.bullets.enableBody = true;
    this.bullets.physicsBodyType = Phaser.Physics.ARCADE;
    this.bullets.createMultiple(30, 'bullet');
    this.bullets.setAll('anchor.x', 0.5);
    this.bullets.setAll('anchor.y', 1);
    this.bullets.setAll('outOfBoundsKill', true);
    this.bullets.setAll('checkWorldBounds', true);

    // The enemy's bullets
    this.enemyBullets = this.game.add.group();
    this.enemyBullets.enableBody = true;
    this.enemyBullets.physicsBodyType = Phaser.Physics.ARCADE;
    this.enemyBullets.createMultiple(30, 'enemyBullet');
    this.enemyBullets.setAll('anchor.x', 0.5);
    this.enemyBullets.setAll('anchor.y', 1);
    this.enemyBullets.setAll('outOfBoundsKill', true);
    this.enemyBullets.setAll('checkWorldBounds', true);

    //  The hero!
    this.player = this.game.add.sprite(400, 500, 'ship');
    this.player.anchor.setTo(0.5, 0.5);
    this.game.physics.enable(this.player, Phaser.Physics.ARCADE);

    //  The baddies!
    this.aliens = this.game.add.group();
    this.aliens.enableBody = true;
    this.aliens.physicsBodyType = Phaser.Physics.ARCADE;

    this.createAliens();

    //  The score
    this.scoreString = 'Score : ';
    this.scoreText = this.game.add.text(10, 10, this.scoreString + this.score, { font: '34px Arial', fill: '#fff' });

    //  Lives
    this.lives = this.game.add.group();
    this.game.add.text(this.game.world.width - 100, 10, 'Lives : ', { font: '34px Arial', fill: '#fff' });

    //  Text
    this.stateText = this.game.add.text(this.game.world.centerX, this.game.world.centerY, ' ', { font: '84px Arial', fill: '#fff' });
    this.stateText.anchor.setTo(0.5, 0.5);
    this.stateText.visible = false;

    for (let i = 0; i < 3; i++) {
      const ship = this.lives.create(this.game.world.width - 100 + (30 * i), 60, 'ship');
      ship.anchor.setTo(0.5, 0.5);
      ship.angle = 0;
      ship.alpha = 0.6;
    }

    //  An explosion pool
    this.explosions = this.game.add.group();
    this.explosions.createMultiple(30, 'kaboom');
    this.explosions.forEach(this.setupInvader, this);

    //  And some controls to play the game with
    this.cursors = this.game.input.keyboard.createCursorKeys();
    this.fireButton = this.game.input.keyboard.addKey(Phaser.Keyboard.SPACEBAR);
  }
  restart () {
    //  A new level starts
    // resets the life count
    this.lives.callAll('revive');
    //  And brings the aliens back from the dead :)
    this.aliens.removeAll();
    this.createAliens();
    // revives the player
    this.player.revive();
    // hides the text
    this.stateText.visible = false;
  }
  collisionHandler (bullet, alien) {
    //  When a bullet hits an alien we kill them both
    bullet.kill();
    alien.kill();

    //  Increase the score
    this.score += 20;
    this.scoreText.text = this.scoreString + this.score;

    //  And create an explosion :)
    const explosion = this.explosions.getFirstExists(false);
    explosion.reset(alien.body.x, alien.body.y);
    explosion.play('kaboom', 30, false, true);

    if (this.aliens.countLiving() === 0) {
      this.score += 1000;
      this.scoreText.text = this.scoreString + this.score;

      this.enemyBullets.callAll('kill', this);
      this.stateText.text = ' You Won, \n Click to restart';
      this.stateText.visible = true;

      // the "click to restart" handler
      this.game.input.onTap.addOnce(() => this.restart(), this);
    }

  }
  enemyHitsPlayer (player, bullet) {
    bullet.kill();

    const live = this.lives.getFirstAlive();
    if (live) { live.kill(); }

    //  And create an explosion :)
    const explosion = this.explosions.getFirstExists(false);
    explosion.reset(player.body.x, player.body.y);
    explosion.play('kaboom', 30, false, true);

    // When the player dies
    if (this.lives.countLiving() < 1) {
      player.kill();
      this.enemyBullets.callAll('kill');

      this.stateText.text = ' GAME OVER \n Click to restart';
      this.stateText.visible = true;

      // the "click to restart" handler
      this.game.input.onTap.addOnce(() => this.restart(), this);
    }
  }
  enemyFires () {
    //  Grab the first bullet we can from the pool
    this.enemyBullet = this.enemyBullets.getFirstExists(false);
    this.livingEnemies.length = 0;

    const self = this;
    this.aliens.forEachAlive((alien) => self.livingEnemies.push(alien));

    if (this.enemyBullet && this.livingEnemies.length > 0) {
      const random = this.game.rnd.integerInRange(0, this.livingEnemies.length - 1);

      // randomly select one of them
      const shooter = this.livingEnemies[random];
      // And fire the bullet from this enemy
      this.enemyBullet.reset(shooter.body.x, shooter.body.y);

      this.game.physics.arcade.moveToObject(this.enemyBullet, this.player, 120);
      this.firingTimer = this.game.time.now + 2000;
    }
  }
  fireBullet () {
    //  To avoid them being allowed to fire too fast we set a time limit
    if (this.game.time.now > this.bulletTime) {
      //  Grab the first bullet we can from the pool
      const bullet = this.bullets.getFirstExists(false);
      if (bullet) {
        //  And fire it
        bullet.reset(this.player.x, this.player.y + 8);
        bullet.body.velocity.y = -400;
        this.bulletTime = this.game.time.now + 200;
      }
    }
  }
  update() {
    //  Scroll the background
    this.starfield.tilePosition.y += 2;

    if (this.player.alive) {
      //  Reset the player, then check for movement keys
      this.player.body.velocity.setTo(0, 0);

      if (this.cursors.left.isDown) {
        this.player.body.velocity.x = -200;
      } else if (this.cursors.right.isDown) {
        this.player.body.velocity.x = 200;
      }

      //  Firing?
      if (this.fireButton.isDown) {
        this.fireBullet();
      }

      if (this.game.time.now > this.firingTimer) {
        this.enemyFires();
      }

      //  Run collision
      this.game.physics.arcade.overlap(this.bullets, this.aliens, (b, a) => this.collisionHandler(b, a), null, this);
      this.game.physics.arcade.overlap(this.enemyBullets, this.player, (p, b) => this.enemyHitsPlayer(p, b), null, this);
    }
  }
  render() {}
  run() {
    const self = this;
    self.running = true;
    self.game = new Phaser.Game(800, 600, Phaser.AUTO, self.invaders.nativeElement, {
      preload: () => self.preload(),
      create: () => self.create(),
      update: () => self.update(),
      render: () => self.render() });
  }
  ngOnInit() {
  }

}



