import { Component, Output, EventEmitter, OnInit, OnDestroy } from '@angular/core';
import { SchemaCacheService, SchemaPage } from '../../services/schema-cache.service';

@Component({
  selector: 'app-schema-browser',
  templateUrl: './schema-browser.component.html'
})
export class SchemaBrowserComponent implements OnInit, OnDestroy {
  @Output() insert: EventEmitter<SchemaPage> = new EventEmitter<SchemaPage>();
  constructor(public cache: SchemaCacheService) {
    const self = this;
    cache.events.subscribe(() => self.checkSchema(cache.schema));
  }
  title = 'Schema';
  schema = null;
  schema_str = '';
  current: any = null;
  checkSchema(schema) {
    this.schema_str = '';
    if (schema) {
      this.schema = schema;
      this.current = schema.model;
    } else {
      this.schema = null;
      this.current = null;
    }
  }
  ngOnInit() {
    this.checkSchema(this.cache.schema);
  }
  ngOnDestroy() {
    // this.cache.events.unsubscribe();
  }
  open(page: SchemaPage) {
    this.current = page;
  }
  notifyAdd(page: SchemaPage) {
    this.insert.emit(page);
  }
  showInfo(page: SchemaPage) {
    this.current = this.cache.getInfoPage(page);
  }
  back() {
    if ((this.current || {}).prev) {
      this.current = this.current.prev;
    }
  }
}



