import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { BrowserService } from '../../services/browser.service';
import { InteractionService } from '../../services/interaction.service';
import { UtilsService } from '../../services/utils.service';
import { UserSettingsService } from '../../services/user-settings.service';
import { CartService } from '../../services/cart.service';
import { DialogsService } from '../../services/dialogs.service';
import _ from 'lodash';
import 'codemirror/mode/javascript/javascript';

const CUSTOM_PATH_PREFIX = 'api/test';

@Component({
  selector: 'app-custom',
  templateUrl: './custom.component.html',
  styleUrls: ['./custom.component.css']
})
export class CustomComponent implements OnInit, OnDestroy {
  @ViewChild('cmeditor') cmeditor: any;
  constructor(public browser: BrowserService,
              private interaction: InteractionService,
              private u: UtilsService,
              private user: UserSettingsService,
              private cart: CartService,
              private dialogs: DialogsService) {
    const self = this;
    self.config = {
      lineNumbers: true,
      mode: 'javascript',
      theme: 'blackboard'
    };
    self.browser.register({
      type: self.interaction.types.custom,
      onSelect: () => self.onSelectCustom(),
      template: {
        name: 'New Service',
        path: 'service',
        active: true,
        calls: []
      }
    });
    self.header = {
      title: () => (self.browser.currentInfo || {}).name,
      isNew: () => (self.browser.current || {})._new,
      buttons: [{
        icon: 'add_circle_outline',
        tooltip: 'Add a new custom service',
        visible: () => !!self.browser.current,
        action: () => self.browser.newElement()
      }, {
        icon: 'save',
        tooltip: 'Save modifies',
        visible: () => !!self.browser.current,
        action: () => {
          self._rebuildPaths(self.browser.current);
          self.browser.save('Service successfully updated!');
        }
      }, {
        icon: 'add_shopping_cart',
        tooltip: 'Add to cart',
        visible: () => !!self.browser.current,
        disabled: () => cart.contains(self.browser.current),
        action: () => cart.add(self.browser.current)
      }, {
        icon: 'delete',
        tooltip: 'Remove service',
        color: 'warn',
        visible: () => !!self.browser.current,
        action: () => self.browser.delete('Delete current virtual service?')
      }]
    };
  }
  header = {};
  config = {};
  currentCall = null;
  static _respIcon(type) {
    switch (type) {
      case 'file':
        return 'insert_drive_file';
      default:
        return 'code';
    }
  }
  private _rebuildPaths(o) {
    const self = this;
    if (o) {
      o.paths = '';
      (o.calls || []).forEach(c => o.paths += (self.u.checkUrl(CUSTOM_PATH_PREFIX, o.path, c.path) + ';'));
    }
  }
  private _replaceSelection(txt) {
    if (!this.cmeditor) { return; }
    const doc = this.cmeditor.instance.getDoc();
    doc.replaceSelection(txt);
  }
  respIcon(type) {
    return CustomComponent._respIcon(type);
  }
  newCall() {
    const self = this;
    const path = self.u.getNewName((self.browser.current || {}).calls, 'new-call', 'path');
    const call = {
      verb: 'get',
      path: path,
      respType: 'object',
      rules: [],
      values: [],
      response: '{}'
    };
    ((self.browser.current || {}).calls || []).push(call);
    self.refreshUrl(call);
    this.currentCall = call;
  }
  selectCall(call) {
    this.currentCall = call;
  }
  deleteCall() {
    const self = this;
    const pos = ((self.browser.current || {}).calls || []).indexOf(this.currentCall);
    if (pos < 0) { return; }
    self.browser.current.calls.splice(pos, 1);
    self.currentCall = null;
  }
  newRule() {
    this.currentCall.rules.push({
      path: '',
      expression: '',
      error: '',
      code: 500
    });
  }
  removeRule(r) {
    _.pull(this.currentCall.rules, r);
  }
  onSelectCustom() {
    const self = this;
    ((self.browser.current || {}).calls || []).forEach(call => {
      call._url = self.user.getUrl(CUSTOM_PATH_PREFIX, self.browser.current.path || 'undefined',
        call.path || 'undefined');
      call.respType = call.respType || 'object';
      if (_.isObject(call.response)) {
        call.response = JSON.stringify(call.response, null, 2);
      }
    });
    self.currentCall = null;
  }
  newValue() {
    const self = this;
    const name = self.u.getNewName((self.browser.current || {}).values, 'value');
    self.currentCall.values.push({
      name: name,
      type: 'manual'
    });
  }
  insertValue(v) {
    this._replaceSelection('{{' + v.name + '}}');
  }
  editValue(v) {
    this.dialogs.editValue(v);
  }
  removeValue(v) {
    _.pull(this.currentCall.values, v);
  }
  refreshUrl(call?: any) {
    const self = this;
    call = call || self.currentCall;
    call._url = self.user.getUrl(CUSTOM_PATH_PREFIX, self.browser.current.path || 'undefined', call.path || 'undefined');
  }
  ngOnInit() {
  }
  ngOnDestroy() {
    this.browser.deregister();
  }
}


