import { Component, OnInit, Input } from '@angular/core';
import {UserSettingsService} from '../../services/user-settings.service';
import {InteractionService} from '../../services/interaction.service';
import {UtilsService} from '../../services/utils.service';
import _ from 'lodash';

@Component({
  selector: 'app-api-tester',
  templateUrl: './api-tester.component.html'
})
export class ApiTesterComponent implements OnInit {
  @Input('test') test: any;
  constructor(private user: UserSettingsService,
              private interaction: InteractionService,
              private u: UtilsService) { }
  go() {
    if (!this.test.running) {
      this.test.running = true;
      this.test.error = null;
      this.interaction.test(this.test, (err, r) => {
        this.test.error = err ? this.u.getErrorMessage(err) : null;
        this.test.response = _.isObject(r) ? JSON.stringify(r, null, 2) : '' + (r || '');
        this.test.running = false;
      });
    }
  }
  close() {
    this.test.show = false;
  }
  ngOnInit() {
    this.test.host = this.test.host || (this.user.settings || {})['host'] || 'http://localhost:9001';
  }

}



