import { Injectable } from '@angular/core';
import { UtilsService } from './utils.service';
import { ParameterEditorComponent } from '../components/parameter-editor/parameter-editor.component';
import { ValueEditorComponent } from '../components/value-editor/value-editor.component';
import {MatDialog} from '@angular/material';
import _ from 'lodash';

@Injectable()
export class DialogsService {

  constructor(private u: UtilsService,
              private dialog: MatDialog) { }
  editParameter(p) {
    const self = this;
    const pe = self.dialog.open(ParameterEditorComponent, {
      width: '500px',
      data: {
        parameter: self.u.clone(p)
      }
    });
    pe.afterClosed().subscribe(op => _.extend(p, op || {}));
  }
  editValue(v) {
    const self = this;
    const pe = self.dialog.open(ValueEditorComponent, {
      width: '500px',
      data: {value: self.u.clone(v)}
    });
    pe.afterClosed().subscribe(ov => _.extend(v, ov || {}));
  }
}



