import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { UtilsService } from '../../services/utils.service';
import _ from 'lodash';

@Component({
  selector: 'app-value-editor',
  templateUrl: './value-editor.component.html'
})
export class ValueEditorComponent implements OnInit {
  constructor(public dialogRef: MatDialogRef<ValueEditorComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any,
              private u: UtilsService) { }
  vTypes = [
    {name: 'Manual', value: 'manual'},
    {name: 'Data', value: 'data'}
  ];
  cDataTypes = [
    {name: 'Text', value: 'string'},
    {name: 'Numeric', value: 'number'},
    {name: 'Integer', value: 'integer'},
    {name: 'Date', value: 'date'},
    {name: 'Boolean', value: 'bool'}
  ];
  checkType() {
    const self = this;
    switch (self.data.value.type) {
      case 'data':
        self.data.value.settings = self.data.value.settings || {};
        _.defaults(self.data.value.settings, {
          autoCol: true,
          columns: [],
          colCount: 8,
          rowCount: 20
        });
        break;
      case 'manual':
        self.data.value.settings = self.data.value.settings || {};
        _.defaults(self.data.value.settings, {
          value: ''
        });
        break;
    }
  }
  addColumn() {
    const name = this.u.getNewName(this.data.value.settings.columns, 'New Column');
    this.data.value.settings.columns.push({
      name: name,
      type: 'string'
    });
  }
  removeColumn(col) {
    _.pull(this.data.value.settings.columns, col);
  }
  isManual() {
    return this.data.value.type === 'manual';
  }
  isData() {
    return this.data.value.type === 'data';
  }
  ngOnInit() {
    const self = this;
    self.data.value = self.data.value || {};
    self.data.value.type = self.data.value.type || 'manual';
    self.data.value.settings = self.data.value.settings || {};
    self.checkType();
  }
  close(v) {
    this.dialogRef.close(v);
  }
}



